.main-head {
  background-color: white;
  box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.06);
}

.main-head--fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}

.main-head__top {
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-head__nav {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.96);
  padding: 20px 0;
  display: none;
  box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.06);
  border-top: 1px solid #ecf1f8;
}

.main-head__nav.open {
  display: block;
}

.main-head__addwr {
  width: 100%;
  position: relative;
}

.main-head__add {
  width: 100%;
  height: 40px;
  position: fixed;
  bottom: 0;
  left: 0;
}

.main-head__add::before,
.main-head__add::after {
  position: absolute;
  top: 50%;
  left: 50%;
  content: "";
  transform: translateX(-50%) translateY(-50%);
  background-color: white;
}

.main-head__add::before {
  width: 20px;
  height: 2px;
}

.main-head__add::after {
  width: 2px;
  height: 20px;
}

