.menu__link {
  position: relative;
  font-size: 14px;
  font-weight: 600;
  display: block;
  padding: 5px 10px;
  text-transform: uppercase;
  color: rgba(62, 63, 88, 0.6);
  text-decoration: none;
  transition: all .5s;
}

.menu__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background-color: transparent;
  transition: all .5s;
}

.menu__link--active {
  color: #3e3f58;
}

.menu__link--active::before {
  background-color: #ed5267;
}

.hbg {
  width: 22px;
  height: 16px;
  position: relative;
  padding: 0;
}

.hbg__line {
  width: 100%;
  position: absolute;
  left: 0;
  border-top: 1px solid;
  border-bottom: 1px solid;
  transform-origin: 50%;
  transition: transform .45s;
}

.hbg__line--top {
  top: 0;
}

.hbg__line--mid {
  top: calc(50% - 1px);
}

.hbg__line--bot {
  top: calc(100% - 2px);
}

.hbg.open .hbg__line--top {
  transform: rotate(45deg);
  top: calc(50% - 1px);
}

.hbg.open .hbg__line--mid {
  display: none;
}

.hbg.open .hbg__line--bot {
  transform: rotate(-45deg);
  top: calc(50% - 1px);
}

.drpd__toggler::before {
  width: 16px;
  height: 16px;
  background-size: 16px;
}

.drpd-menu {
  display: none;
}

.drpd-menu.open {
  display: block;
}

.drpd-menu__list {
  position: absolute;
  top: calc(100% + 16px);
  min-width: 100px;
  background-color: white;
  padding-top: 20px;
  padding-bottom: 20px;
  border-radius: 8px;
  box-shadow: 0px 24px 24px rgba(0, 0, 0, 0.12);
  z-index: 99;
}

.drpd-menu__link {
  font-weight: 600;
  white-space: nowrap;
  display: flex;
  align-items: center;
