}

@media (min-width: 768px) {
  .supwrap {
    max-width: 750px;
  }

  .icns-list__itm--hm {
    display: block;
  }

  .card-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .card-list__itm {
    width: calc(50% - 12px);
  }
}

@media (min-width: 992px) {
  .supwrap {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .layout--has-fixed-head {
    padding-top: 112px;
  }

  .layout--has-fixed-btn {
    padding-bottom: 0;
  }

  .supwrap {
    max-width: 1200px;
  }

  .main-head__top {
    padding-top: 21px;
  }

  .main-head__hbg {
    display: none;
  }

  .main-head__nav {
    position: relative;
    top: auto;
    left: auto;
    display: block;
    box-shadow: none;
    border-top: none;
    padding: 0px;
  }

  .main-head__add {
    width: 64px;
    height: 64px;
    position: absolute;
    top: 0%;
    right: 0;
    left: auto;
    transform: translateX(-15px) translateY(-50%);
    border-radius: 50%;
  }

  .status-bar__icons {
    order: 2;
  }

  .status-bar__mnu {
    order: 1;
    margin-right: 20px;
  }

  .user-info__name {
    display: block;
    margin-right: 20px;
  }

  .user-info__logged {
    padding-right: 30px;
  }

  .user-info__dml {
    right: 0;
  }

  .menu {
    display: flex;
  }

  .menu__itm:not(:first-child) {
    margin-left: 37px;
  }

  .menu__link {
    padding: 5px 0px 20px;
  }

  .menu__link::before {
    top: auto;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 4px;
    transform: translateX(-50%);
  }

  .menu__link--active::before {
    width: 100%;
    height: 4px;
  }

  .menu__link:hover {
    color: #3e3f58;
  }

  .menu__link:hover::before {
    width: 100%;
    height: 4px;
    background-color: #ed5267;
  }

  .drpd-menu__itm:hover {
    background-color: #ecf1f8;
  }

  .content__parts {
    display: flex;
    padding-top: 72px;
  }

  .content__part--l {
    flex: 0 0 374px;
    padding-right: 24px;
  }

  .content__part--r {
